import { useContext, useEffect, useState } from "react";
import { IOConnectContext } from "./Glue";
import { IOConnectBrowser } from "@interopio/browser";
import { Glue42 } from "@glue42/desktop";

export const useIOConnect = <K = IOConnectBrowser.API | Glue42.Glue, T = void>(
    cb: (io: K, ...dependencies: any[]) => Promise<T> | T,
    dependencies: any[] = []
): T => {
    const [result, setResult] = useState<T>();
    const io: any = useContext(IOConnectContext);
    useEffect(() => {
        const callback = async () => {
            try {
                const result = await cb(io, ...dependencies);
                typeof result !== "undefined" &&
                    setResult(typeof result === "function" ? () => result : result);
            } catch (e) {
                console.error(e);
            }
        };
        io && callback();
    }, [io, ...dependencies]);
    return result;
};
